
import { defineComponent } from 'vue';
import Header from '@/components/Header.vue';
import Footer from '@/components/Footer.vue';
import TablaUsuarios from '@/components/TablaUsuarios.vue';
import FormularioUsuario from '@/components/FormularioUsuario.vue';
import IUser from '@/interfaces/IUser';
import apiUsers from '@/services/users';

export default defineComponent({
  name: 'App',

  // Mis componentes
  components: {
    Header,
    Footer,
    TablaUsuarios,
    FormularioUsuario,
  },

  // Mi modelo de datos
  data: () => ({
    usuarios: [] as IUser[],
  }),

  // Mi ciclo de vida
  /**
   * Al crerame, consultamos los usuarios
   */
  async created() {
    await this.findAll();
  },

  // Mis métodos
  methods: {
    /**
     * Obtiene toda la lista de usuarios
     */
    async findAll() {
      try {
        this.usuarios = await apiUsers.findAll();
      } catch (err) {
        console.log(err);
      }
    },
    /**
     * Borramos un usuario en la api y en nuestro modelo de datos
     */
    async remove(user: IUser) {
      try {
        await apiUsers.remove(user);
        this.usuarios = this.usuarios.filter((u) => u.id !== user.id);
      } catch (err) {
        console.log(err);
      }
    },

    /**
     * Actualiza un usuario a través de la Api Rest, y en nuestro modelo de datos
     */
    async update(user: IUser) {
      try {
        const usuarioActualizado = await apiUsers.update(user);
        this.usuarios = this.usuarios.map((u) => (u.id === user.id ? usuarioActualizado : u));
      } catch (error) {
        console.error(error);
      }
    },

    /**
     * Salva un usuario a través de la Api Rest, y en nuestro modelo de datos
     */
    async save(user: IUser) {
      try {
        const usuarioCreado = await apiUsers.save(user);
        this.usuarios = [...this.usuarios, usuarioCreado];
      } catch (error) {
        console.error(error);
      }
    },
  },
});
